.title {
  max-width: 370px;
  font-weight: 600;
  padding-top: 164px;

  &__head {
    font-size: 64px;
    line-height: 1;
    letter-spacing: -2px;
  }

  &__text {
    margin: 32px 0 56px;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.5;
  }
}

@media screen and (max-width: $max-screen) {
  .title--origin {
    display: none;
  }

  .title {
    padding-top: 60px;

    &__head {
      font-size: 48px;
    }

    &__text {
      font-size: 14px;
    }
  }
}
